/* stylelint-disable */
.header {

  &-top {
    background: linear-gradient(115deg, rgba(86, 204, 242, 1) 71%,
        rgba(255, 255, 255, 1) 71%);
    padding-bottom: 400px;

    &__h1,
    &__quote,
    &__button {
      margin-left: 325px;
    }

    &__h1 {
      margin-top: 160px;
      font-weight: 900;
      font-size: 64px;
      line-height: 150%;
    }

    &__quote {
      font-size: 24px;
      line-height: 150%;
      margin-top: 22px;
    }

    &__button {
      display: inline-block;
      margin-top: 42px;
    }
  }

  &-bottom {
    position: relative;
    top: -320px;

    &__slider {
      position: relative;
      width: calc(100% - 227px);
      margin: 0 auto;
      height: 640px;
      background-size: cover;
      background-image: url(../images/slider/slide-img-1.jpg);

      .switch {
        position: absolute;
        width: 162px;
        height: 64px;
        left: -81px;
        top: 286px;
        background: linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%,
            rgba(255, 255, 255, 0) 100%),
          #fcc304;
        border-radius: 50px;

        &__arrow--left,
        &__arrow--right {
          width: 24px;
          height: 12px;
          position: absolute;
        }

        &__arrow--left {
          top: 20px;
          left: 33px;
        }

        &__arrow--right {
          top: 20px;
          right: 33px;
        }
      }

      .intro {
        position: absolute;
        top: 477px;
        left: 98px;
        font-weight: 800;
        font-size: 13px;
        line-height: 150%;
        letter-spacing: 2px;
        color: #56ccf2;
      }

      .intro-quote {
        position: absolute;
        top: 500px;
        left: 98px;
        font-weight: 900;
        font-size: 20px;
        line-height: 140%;
        color: #fff;
      }
    }

    &__social {
      display: flex;
      align-items: center;
      transform: rotate(90deg);
      position: absolute;
      left: calc(100% - 291px);
      top: 340px;

      &>* {
        font-weight: 800;
        font-size: 13px;
        line-height: 150%;
        letter-spacing: 2px;
        text-transform: uppercase;
        color: #253757;
        margin-right: 85px;
      }
    }

  }
}

@media (min-width: 1100px) and (max-width: 1350px) {

  .header {

    &-top {

      &__h1 {
        margin-left: 150px;
      }

      &__quote {
        margin-left: 150px;
      }
    }
  }
}

@media (min-width: 1000px) and (max-width: 1100px) {

  .header {

    &-top {

      &__h1 {
        margin-left: 100px;
      }

      &__quote {
        margin-left: 70px;
      }
    }
  }

}

@media (max-width: 999px) {
  .header {

    &-top {
      background: rgba(86, 204, 242, 1);

      &__h1,
      &__quote,
      &__button {
        margin-left: 100px;
      }

      &__h1 {
        margin-top: 60px;
        font-size: 35px;
      }

      &__quote {
        font-size: 20px;
      }
    }

    &-bottom {

      &__social {
        left: calc(100% - 300px);
      }

      .intro,
      .intro-quote {
        left: 10px;
      }
    }
  }
}
/* stylelint-enable */
